.selectMark{
   width: 224px;
   padding: 14px 24px 14px 18px;
   border-radius: 14px;
   background: #F7F7FB;
}

.selectPrice {
   width: 165px;
   padding: 14px 27px 14px 18px;
   border-radius: 14px;
   background: #F7F7FB;
}

.sectionSearch{
   display: flex;
   gap: 18px;
   justify-content: center;
   align-items: flex-end;
}

.blockSelect{
   display: flex;
   flex-direction: column;
   align-items: flex-start;
   gap: 8px;
}

.blockSelectMarkName{
   color: #8A8A89;
   font-family: Manrope;
   font-size: 14px;
   font-weight: 500;
   line-height: 18px;
}

.buttomSearch{
   color: var(--White, #FFF);
   font-family: Manrope;
   font-size: 14px;
   font-weight: 600;
   line-height: 20px;

   padding: 14px 44px;
   border-radius: 12px;
   background: #3470FF;
   border-color: transparent;
}

.forms{
   display: flex;
   align-items: center;
}

.formFrom{
   padding: 14px 0px 14px 24px;
   border-radius: 14px 0px 0px 14px;
   border-right: 1px solid rgba(138, 138, 137, 0.20);
   background: #F7F7FB;
   border-top-color: transparent;
   border-left-color: transparent ;
   border-bottom-color: transparent ;
}

.formTo{
   padding: 14px 0px 14px 24px;
   border-radius: 0px 14px 14px 0px;
   background: #F7F7FB;
   color: #121417;
   border-color: transparent;
   
}

/* оголошення */
.advertisementList{
   display: flex;
   flex-wrap: wrap;
   column-gap: 2%;
   justify-content: center;
   margin-top: 50px;
}

.imageContainer {
   position: relative;
   display: inline-block;
 }
 
 .heartIcon {
   position: absolute;
   top: 16px;
   right: 16px;
   width: 18px; 
   height: 18px; 
   z-index: 1; 
}

.imgCars{
   width: 274px;
   height: 268px;
   border-radius: 14px;
   object-fit: cover;
   object-position: center;
   margin-bottom: 14px;
}

.infoCar{
   display: flex;
width: 274px;
flex-direction: column;
align-items: flex-start;
gap: 8px;
}

.h2NameModalCars{
   display: flex;
   color: #121417;
font-family: Manrope;
font-size: 16px;
font-weight: 500;
line-height: 24px;
margin: 0;
gap: 2px;
}

.blockInfo{
   color: rgba(18, 20, 23, 0.50);
font-family: Manrope;
font-size: 12px;
font-weight: 400;
line-height: 18px;
}

.nameModal{
   color: #3470FF;
font-family: Manrope;
font-size: 16px;
font-weight: 500;
line-height: 24px;
}

.blockModalAndPrice{
   display: flex;
   align-items: flex-start;
   width: 265px;
   height: 24px;
   justify-content: space-between;
}

p{
   margin: 0;
}

.LearnMore{
   display: flex;
   justify-content: center;
   align-items: center;
   width: 274px;
   height: 44px;
   padding: 12px 99px;
   margin-top: 28px;
   margin-bottom: 50px;
   color: var(--White, #FFF);
   font-family: Manrope;
   font-size: 14px;
   font-weight: 600;
   line-height: 20px; 
   border-radius: 12px;
   background: #3470FF;
   border-color: transparent;

}

.loadMore{
   color: #3470FF;
   font-family: Manrope;
   font-size: 16px;
   font-weight: 500;
   line-height: 24px; 
   text-decoration-line: underline;
   margin-top: 100px;
   display: flex;
   justify-content: center;
   margin-bottom: 150px;
   cursor: pointer;
}